<template>
  <div>
    <div :class="{active: isActive}">{{message}}</div>
    <div class="clock">
      <div class="hourHand" v-bind:style="{transform:shi}"></div>
      <div class="minuteHand" v-bind:style="{transform:fen}"></div>
      <div class="secondHand" v-bind:style="{transform:miao}"></div>
      <div class="center"></div>
      <div class="time">
        <span>
          <strong>{{hour}}</strong>:{{minute}}:{{second}}
        </span>
      </div>
      <ul>
        <li>
          <span>1</span>
        </li>
        <li>
          <span>2</span>
        </li>
        <li>
          <span>3</span>
        </li>
        <li>
          <span>4</span>
        </li>
        <li>
          <span>5</span>
        </li>
        <li>
          <span>6</span>
        </li>
        <li>
          <span>7</span>
        </li>
        <li>
          <span>8</span>
        </li>
        <li>
          <span>9</span>
        </li>
        <li>
          <span>10</span>
        </li>
        <li>
          <span>11</span>
        </li>
        <li>
          <span>12</span>
        </li>
      </ul>
    </div>
  </div>

</template>

<script>
export default {
  message2: "",
  name: "Test",
  data() {
    return {
      isActive: true,
      message: "",
      today: "",
      hour: "",
      minute: "",
      second: "",
      shi: "",
      fen: "",
      miao: ""
    };
  },
  mounted: function() {
    this.go();
  },
  methods: {
    show() {
      const today = new Date();
      this.second = today.getSeconds(); //获得秒
      const secondDeg = this.second / 60 * 360 + 360;
      this.miao = "rotate(" + secondDeg + "deg)";

      this.minute = today.getMinutes(); //获得秒
      const minuteDeg = this.minute / 60 * 360;
      this.fen = "rotate(" + minuteDeg + "deg)";

      this.hour = today.getHours(); //获得秒
      const hourDeg = this.hour / 12 * 360;
      this.shi = "rotate(" + hourDeg + "deg)";
    },
    go(){
       setInterval(() => {
        this.show();
      }, 1000);
    }
  }
};
</script>

<style>
body {
  background-color: coral;
  padding: 0;
  margin: 0;
  background: url(/static/img/zhudi.jpg);
  background-size: 130%;
  background-position-x: 50%;

}

.clock {
    width: 230px;
    height: 230px;
    border-radius: 50%;
    background-color: #fe8e76;
    margin: auto;
    margin-top: 156px;
    position: relative;
    border: 20px solid #ffcccb;
}
.center {
  background-color: #6c5950;
  position: absolute;
  left: calc(50% - 10px);
  top: calc(50% - 10px);
  width: 20px;
  height: 20px;
  border-radius: 50%;
  z-index: 20;
}
.hourHand {
  width: 10px;
  height: 55px;
  background-color: #6b6b6d;
  transform-origin: bottom center;
  border-radius: 4px;
  position: absolute;
  top: 62px;
  left: 110px;
  z-index: 10;
  transition-timing-function: cubic-bezier(0.1, 2.7, 0.58, 1);
  transform: rotate(360deg);
}
.minuteHand {
  width: 5px;
  height: 75px;
  background-color: #94c8fe;
  transform-origin: bottom center;
  border-radius: 4px;
  position: absolute;
  top: 39px;
  left: 113px;
  z-index: 9;
  transition-timing-function: cubic-bezier(0.1, 2.7, 0.58, 1);
  transform: rotate(90deg);
}
.secondHand {
  width: 2px;
  height: 95px;
  background-color: red;
  transform-origin: bottom center;
  border-radius: 4px;
  position: absolute;
  top: 20px;
  left: 113px;
  transition: all 0.06s;
  transition-timing-function: cubic-bezier(0.1, 2.7, 0.58, 1);
  z-index: 8;
  transform: rotate(360deg);
}
.time {
  position: absolute;
  top: 45%;
  left: 10%;
  border: 1px solid #fff8dc;
  background-color: #fff;
  padding: 5px;
  display: block;
  box-shadow: inset 0px 2px 5px rgba(0, 0, 0, 0.4);
  border-radius: 5px;
  min-width: 70px;
  height: 15px;
}
.time small {
  color: red;
  transition: all 0.05s;
  transition-timing-function: cubic-bezier(0.1, 2.7, 0.58, 1);
}

.clock ul {
  list-style: none;
  padding: 0;
}
.clock ul li {
  position: absolute;
  width: 20px;
  height: 20px;
  text-align: center;
  line-height: 20px;
  font-size: 10px;
  color: #fff;
}
.clock ul li:nth-child(1) {
  right: 22%;
  top: 6.5%;
}
.clock ul li:nth-child(2) {
  right: 6%;
  top: 25%;
}
.clock ul li:nth-child(3) {
  right: 1%;
  top: calc(50% - 10px);
  color: #f5e934;
  font-size: 20px;
  font-weight: bold;
}
.clock ul li:nth-child(4) {
  right: 6%;
  top: 69%;
}
.clock ul li:nth-child(5) {
  right: 22%;
  top: 84%;
}
.clock ul li:nth-child(6) {
  right: calc(50% - 10px);
  top: calc(99% - 20px);
  color: #f5e934;
  font-size: 20px;
  font-weight: bold;
}
.clock ul li:nth-child(7) {
  left: 22%;
  top: 84%;
}
.clock ul li:nth-child(8) {
  left: 6%;
  top: 69%;
}
.clock ul li:nth-child(9) {
  left: 1%;
  top: calc(50% - 10px);
  color: #f5e934;
  font-size: 20px;
  font-weight: bold;
}
.clock ul li:nth-child(10) {
  left: 6%;
  top: 25%;
}
.clock ul li:nth-child(11) {
  left: 22%;
  top: 6.5%;
}
.clock ul li:nth-child(12) {
  right: calc(50% - 10px);
  top: 1%;
  color: #f5e934;
  font-size: 20px;
  font-weight: bold;
}
.active {
  color: #ffffff;
  font-weight: bold;
  text-align: center;
  font-size: 20px;
  margin-top: 20px;
}
</style>


